@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  min-height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  min-width: 320px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  color: inherit;
}

img {
  display: block;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.page__services {
  margin-bottom: 140px;
}

.page__about {
  margin-bottom: 140px;
}

.header {
  padding-top: 12px;
  padding-right: 16px;
  padding-left: 16px;
  padding-bottom: 30px;
  color: hsl(0deg, 100%, 100%);
  background-color: hsl(0deg, 100%, 0%);
  background-image: url("../img/background/smoke@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/background/smoke@2x.jpg");
  }
}

.header__navigation {
  margin-bottom: 130px;
}

.header__title {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
}

.header__description {
  display: block;
  max-width: 600px;
  margin-top: 0;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 30px;
  font-size: 17px;
  line-height: 23px;
  text-align: center;
}

.header__button {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 200px;
}

.navigation {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
}
@media (max-width: 820px) {
  .navigation {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 520px) {
  .navigation {
    grid-template-columns: 1fr 2fr;
  }
}

.navigation__button-logo {
  margin-right: 10px;
}

.navigation__menu-list {
  justify-self: center;
  margin-top: 0;
  margin-bottom: 0;
}

.navigation__contact-list {
  justify-self: right;
  margin: 0;
}

.button-logo {
  display: flex;
  align-items: center;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.button-logo__img {
  margin-right: 10px;
}

.button-logo__text {
  font-size: 14px;
  font-weight: 700;
}
@media (max-width: 520px) {
  .button-logo__text {
    display: none;
  }
}

.menu-list {
  display: flex;
  align-items: center;
  padding: 0;
  list-style: none;
}
@media (max-width: 820px) {
  .menu-list {
    display: none;
  }
}

.menu-list__item {
  margin-right: 20px;
}
.menu-list__item:last-child {
  margin-right: 0;
}

.menu-list__link {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.7);
  transition: color 0.2s;
}
.menu-list__link:hover {
  color: hsl(0deg, 100%, 100%);
}

.menu-list__link--active {
  font-weight: 700;
  color: hsl(0deg, 100%, 100%);
}

.contact-list {
  display: flex;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.contact-list__item {
  margin-right: 15px;
}

.contact-list__link {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.7);
  transition: color 0.2s;
}
.contact-list__link:hover {
  color: hsl(0deg, 100%, 100%);
}

.contact-list__link--vk {
  display: block;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 12px;
  padding-left: 12px;
  border-radius: 8px;
  color: hsl(0deg, 100%, 100%);
  background-color: rgba(255, 255, 255, 0.2);
  transition: background-color 0.2s, transform 0.3s;
}
.contact-list__link--vk:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
.contact-list__link--vk:active {
  transform: scale(0.9);
}
@media (max-width: 1120px) {
  .contact-list__link--vk {
    display: none;
  }
}

.container {
  max-width: 1280px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

