  * Copyright 2020 - 2021 Alexander Maltsev
  * Licensed under MIT (https://github.com/itchief/ui-components/blob/master/LICENSE)
  */
/* стили для корневого элемента слайдера */
.itcss {
  position: relative;
  overflow: hidden;
  max-width: 680px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-right: 40px;
  padding-left: 40px;
}

/* стили для wrapper */
.itcss__wrapper {
  position: relative;
  overflow: hidden;
  background-color: transparent;
}

/* стили для элемента, в котором непосредственно расположены элементы (слайды) */
.itcss__items {
  display: flex;
  transition: transform 0.5s ease;
}

/* класс для отключения transition */
.transition-none {
  transition: none;
}

/* стили для элементов */
.itcss__item {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
}
@media (max-width: 820px) {
  .itcss__item {
    font-size: 16px;
    line-height: 20px;
  }
}

/* кнопки Prev и Next */
.itcss__control {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 50px;
  transform: translateY(-50%);
  display: none;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: transparent;
  opacity: 0.5;
  user-select: none;
}

.itcss__control_show {
  display: flex;
}

.itcss__control:hover,
.itcss__control:focus {
  color: #fff;
  text-decoration: none;
  opacity: 0.7;
}

.itcss__control_prev {
  left: 0;
}

.itcss__control_next {
  right: 0;
}

.itcss__control::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  background: transparent no-repeat center center;
  background-size: 100% 100%;
}

.itcss__control_prev::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.itcss__control_next::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

/* Индикаторы */
.itcss__indicators {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin: 0 15%;
  list-style: none;
  user-select: none;
}

.itcss__indicator {
  flex: 0 1 auto;
  width: 30px;
  height: 4px;
  margin-right: 3px;
  margin-left: 3px;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
}

.itcss__indicator_active {
  background-color: #fff;
}

.title {
  font-size: 44px;
  font-weight: 400;
  line-height: 54px;
}
@media (max-width: 820px) {
  .title {
    font-size: 34px;
    line-height: 42px;
  }
}

.title--sub {
  font-size: 30px;
  line-height: 38px;
}
@media (max-width: 820px) {
  .title--sub {
    font-size: 24px;
    line-height: 32px;
  }
}

.button {
  display: flex;
  align-items: center;
  width: fit-content;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-right: 16px;
  padding-left: 14px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  color: hsl(0deg, 100%, 100%);
  cursor: pointer;
  background-color: hsl(209deg, 74%, 49%);
}
.button path {
  display: block;
  fill: hsl(0deg, 100%, 100%);
  width: 100%;
  height: 100%;
}

.button--dark {
  color: hsl(0deg, 100%, 0%);
  background-color: hsl(0deg, 100%, 100%);
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
}
.button--dark path {
  fill: hsl(0deg, 100%, 0%);
  transition: fill 0.3s ease;
}
.button--dark:hover {
  background-color: hsl(0deg, 100%, 0%);
  color: hsl(0deg, 100%, 100%);
}
.button--dark:hover path {
  fill: hsl(0deg, 100%, 100%);
}
.button--dark:active {
  transform: scale(0.9);
}

.button__icon {
  display: block;
  margin-right: 8px;
}

.services {
  padding-top: 40px;
  background-color: hsl(0deg, 100%, 100%);
}

.services__title {
  margin-top: 0;
  margin-bottom: 40px;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 274px);
  column-gap: 40px;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 20px;
  padding-left: 0;
  list-style: none;
}
@media (max-width: 820px) {
  .services__list {
    grid-template-columns: repeat(auto-fill, 200px);
  }
}
@media (max-width: 540px) {
  .services__list {
    grid-template-columns: 1fr;
    row-gap: 40px;
  }
}

.services__item {
  cursor: pointer;
}

.services__img {
  width: 100%;
  height: auto;
  margin-right: 0;
  margin-bottom: 10px;
  transition: transform 0.3s;
}
.services__img:hover {
  transform: scale(1.2);
}

.services__item-description {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 24px;
}
@media (max-width: 820px) {
  .services__item-description {
    font-size: 16px;
    line-height: 20px;
  }
}

.services__item-price {
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}

.about {
  background-color: hsl(0deg, 100%, 100%);
}

.about__title {
  margin-top: 0;
  margin-bottom: 30px;
  text-align: center;
}

.about__text {
  display: block;
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: hsl(0deg, 0%, 60%);
}

.contacts {
  margin-bottom: 100px;
}

.contacts__title {
  margin-top: 0;
  margin-bottom: 30px;
}

.contacts__inner {
  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 40px;
}
@media (max-width: 820px) {
  .contacts__inner {
    grid-template-columns: 1fr;
  }
}

.contacts__map {
  width: 100%;
  height: 350px;
  background-color: hsl(0deg, 0%, 60%);
}
@media (max-width: 820px) {
  .contacts__map {
    margin-bottom: 30px;
  }
}

.contacts__phone,
.contacts__link {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 18px;
  color: hsl(209deg, 74%, 49%);
  transition: color 0.2s;
}
.contacts__phone:hover,
.contacts__link:hover {
  color: rgba(32, 128, 217, 0.7);
}
.contacts__phone:active,
.contacts__link:active {
  color: rgba(32, 128, 217, 0.5);
}

.contacts__time {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 18px;
}

.footer {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: hsl(0deg, 0%, 80%);
}

.footer__container {
  display: flex;
  justify-content: center;
}

.footer__developer {
  position: relative;
  display: inline-block;
  color: hsl(0deg, 100%, 0%);
  text-align: center;
}
.footer__developer::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  display: block;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0);
  transition: bottom 0.3s, background-color 0.3s;
}
.footer__developer:hover::after {
  bottom: 0;
  background-color: hsl(0deg, 100%, 0%);
}